.input {
    caret-color: red;
}

.container {
    position: relative;
    width: 100%;
    height: fit-content;
    border-radius: 20px;
    padding: 15px;
    box-sizing: border-box;
    background: #ecf0f3;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    /* box-shadow: 14px 14px 20px #cbced1, -14px -14px 20px white; */
}

.connectionSection {
    display: flex;
    flex-direction: column;
    width: 40%;
}

.brand_logo {
    height: 100px;
    width: 100px;
    background: url("./image/wollenWirBrannenSVG.svg");
    margin: auto;
    border-radius: 50%;
    box-sizing: border-box;
    background-size: cover;
    box-shadow: 7px 7px 10px #cbced1, -7px -7px 10px white;
}

.brand_title {
    margin: 0 0 10px 0;
    font-weight: 900;
    color: #1da1f2;
    letter-spacing: 1px;
    font-size: 25px;
    max-width: 80%;
}

.inputs {
    text-align: left;
    width: 100%;
}

.input,
.button {
    display: block;
    width: 100%;
    padding: 0;
    border: none;
    outline: none;
    box-sizing: border-box;
}

.input:nth-of-type(2) {
    margin-top: 12px;
}

.input::placeholder {
    color: gray;
}

.input {
    background: #ecf0f3;
    padding: 10px;
    padding-left: 20px;
    height: 50px;
    font-size: 14px;
    border-radius: 50px;
    box-shadow: inset 6px 6px 6px #cbced1, inset -6px -6px 6px white;
}

.button {
    color: white;
    margin-top: 20px;
    background: #1da1f2;
    height: 40px;
    border-radius: 20px;
    cursor: pointer;
    font-weight: 900;
    box-shadow: 6px 6px 6px #cbced1, -6px -6px 6px white;
    transition: 0.5s;
}

.button:hover {
    box-shadow: none;
}

.title {
    position: absolute;
    top: 0;
    left: 0;
}
